.parent {
  @apply flex justify-center items-center flex-col m-2  sm:m-8  overflow-hidden;

  .heading {
    @apply text-2xl font-bold text-center my-6 text-white relative;
  }

  input {
    @apply block w-80   bg-white bg-opacity-10 border-r-2 border-transparent  py-3  px-4 placeholder:text-slate-400 outline-none text-slate-50 transition-colors rounded-t-xl;
  }

  select {
    @apply block w-80 mb-5  bg-white bg-opacity-50 py-3  px-4 placeholder:text-slate-400 outline-none rounded-b-xl text-slate-50 transition-colors;
    option {
      @apply text-black bg-slate-600;
    }
  }

  ul {
    @apply mt-2 flex justify-center flex-wrap gap-x-3 gap-y-1.5;
    li {
      @apply cursor-pointer flex justify-between items-center text-xs w-40 sm:w-64 sm:h-32 sm:text-lg bg-slate-700 text-gray-400 shadow-lg mb-2  rounded-md p-2 pr-1;
      img {
        @apply h-9 w-10 sm:h-20 sm:w-20 bg-transparent;
      }
      span {
        @apply font-bold text-white text-xs sm:text-lg;
      }
    }
  }
}
